import { url } from '@/config'
import request from '../axios'
let origin = location.origin
export const getList = (params) =>
  request({
    //url: url + '/map/list',
    url: origin + '/bladeVisualMaps',
    method: 'get',
    params: params,
    transformResponse: [
      function(data) {
        let newData = JSON.parse(data)
        newData.data = {}
        newData.data.records = newData.page.records
        return newData
      },
    ],
  })

export const getObj = (id) =>
  request({
    //url: url + '/map/detail',
    url: origin + `/bladeVisualMaps/${id}`,
    method: 'get',
    transformResponse: [
      function(data) {
        console.log(123, data)
        return data
      },
    ],
  })

export const addObj = (data) =>
  request({
    url: url + '/map/save',
    method: 'post',
    data: data,
  })
export const updateObj = (data) =>
  request({
    url: url + '/map/update',
    method: 'post',
    data: data,
  })

export const delObj = (id) =>
  request({
    url: url + '/map/remove',
    method: 'post',
    params: {
      ids: id,
    },
  })
